
'use client';

import { useEffect, useState } from 'react';
import { useRouter, useSearchParams } from 'next/navigation';
import { useAuth } from '@/hooks/use-auth';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Loader2, ShieldQuestion, UserCheck } from 'lucide-react';
import { doc, setDoc } from 'firebase/firestore';
import { useToast } from '@/hooks/use-toast';
import { RotaScribeIcon } from '@/components/icons/RotaScribeIcon';
import { useFirebase } from '@/firebase';


export default function RoleSelectionPage() {
  const { firebaseUser, isUserLoading } = useAuth();
  const { firestore } = useFirebase();
  const router = useRouter();
  const searchParams = useSearchParams();
  const [isSubmitting, setIsSubmitting] = useState(false);
  const { toast } = useToast();

  const churchIdFromUrl = searchParams.get('churchId');

  useEffect(() => {
    // If user is not logged in or still loading, do nothing, the AuthProvider will handle redirection.
    if (!isUserLoading && !firebaseUser) {
      router.push('/');
    }
  }, [firebaseUser, isUserLoading, router]);

  const handleRoleSelection = async (role: 'admin' | 'volunteer') => {
    if (!firebaseUser || !firestore) return;

    // Use churchId from URL for volunteers, or a create a new one for new admins
    const churchId = role === 'admin' ? `church_${Date.now()}` : churchIdFromUrl;

    if (role === 'volunteer' && !churchId) {
      toast({
        variant: 'destructive',
        title: 'Missing Information',
        description: 'The church ID is missing from the signup link. Please use a valid link.',
      });
      return;
    }

    setIsSubmitting(true);
    
    try {
      const userDocRef = doc(firestore, 'users', firebaseUser.uid);
      await setDoc(userDocRef, {
        id: firebaseUser.uid,
        name: firebaseUser.displayName,
        email: firebaseUser.email,
        role: role,
        churchId: churchId,
        preferences: {
            preferredRoleIds: [],
            servingFrequency: 'as-needed',
            familyServingPreference: 'any',
            familyManagementEnabled: false,
        }
      }, { merge: true });
      
      // Store a flag to indicate setup is complete for this user
      sessionStorage.setItem(`role_selected_${firebaseUser.uid}`, 'true');

      if (role === 'admin') {
        // New admins go to the setup wizard
        router.push('/admin/setup-wizard');
      } else {
        // New volunteers go to their schedule
        router.push('/my-schedule');
      }
    } catch (e: any) {
        console.error(e);
        toast({
            variant: 'destructive',
            title: 'Error Saving Role',
            description: e.message || 'Could not save your role selection. Please try again.',
        });
        setIsSubmitting(false);
    }
  };

  if (isUserLoading || !firebaseUser) {
    return (
      <div className="flex min-h-screen items-center justify-center">
        <Loader2 className="h-16 w-16 animate-spin text-primary" />
      </div>
    );
  }

  return (
    <main className="flex min-h-screen flex-col items-center justify-center p-8 bg-muted/40">
      <div className="text-center mb-8">
        <RotaScribeIcon className="h-12 w-12 text-primary mx-auto mb-4" />
        <h1 className="text-4xl font-bold font-headline text-foreground tracking-tight sm:text-5xl">
          Welcome, {firebaseUser.displayName}!
        </h1>
        <p className="mt-3 text-lg text-foreground/80">
          One last step. How will you be using RotaScribe?
        </p>
      </div>

      <Card className="w-full max-w-lg animate-fade-in">
        <CardHeader>
          <CardTitle className="text-center">Choose Your Role</CardTitle>
          <CardDescription className="text-center">This helps us tailor your experience.</CardDescription>
        </CardHeader>
        <CardContent className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <Button
            variant="outline"
            className="h-auto p-6 flex flex-col items-center gap-2"
            onClick={() => handleRoleSelection('volunteer')}
            disabled={isSubmitting || !churchIdFromUrl}
            title={!churchIdFromUrl ? "You must use a valid signup link to join as a volunteer." : ""}
          >
            <UserCheck className="h-10 w-10 text-primary" />
            <span className="font-semibold text-lg">I'm a Volunteer</span>
            <span className="text-xs text-muted-foreground text-center">I need to see my schedule and manage my availability.</span>
          </Button>
          <Button
            variant="outline"
            className="h-auto p-6 flex flex-col items-center gap-2"
            onClick={() => handleRoleSelection('admin')}
            disabled={isSubmitting}
          >
            <ShieldQuestion className="h-10 w-10 text-primary" />
            <span className="font-semibold text-lg">I'm an Administrator</span>
            <span className="text-xs text-muted-foreground text-center">I need to create events and manage the schedule for others.</span>
          </Button>
        </CardContent>
        {isSubmitting && (
            <div className="p-4 pt-0 flex items-center justify-center text-muted-foreground">
                <Loader2 className="mr-2 h-4 w-4 animate-spin"/>
                Saving your role...
            </div>
        )}
      </Card>
    </main>
  );
}
